const Main = () => { 
    const [checked, setChecked] = useState(true);

    const panelText = panel => <Box gap="medium"><Text>{panel.body}</Text></Box>
    const panelList = panel =>
        <List data={panel.list} children={(datum, i) =>
            <Box direction="row">
                <Box pad={{top: "xxsmall"}}>
                    <Home size="small"/>
                </Box>
                <Box pad={{left: "small"}}>
                    <Text>{ datum }</Text>
                </Box>
            </Box>}/>
    const panelTable = panel => 
        <Box align="center" pad="medium">
            <DataTable
                columns={conveyancerDataColumns}
                data={panel.table}
                resizeable
            />
        </Box> 
    const panelLink = panel => 
        <Box pad={{top: "small"}} direction="row">
            <Copy /><Anchor href={panel.link.url} target="_blank" label={panel.link.label} margin={{left:"xxsmall"}}/>
        </Box>

    const render = panel => 
        <AccordionPanel key={panel.header} label={<Text margin={{left:"xsmall", vertical: "small"}} size="large" weight={0}>{panel.header}</Text>}>
            <Box pad={{left: "xsmall", bottom: "medium"}}>
                { panelText(panel) }
                { panel.list ? panelList(panel) : null }
                { panel.table ? panelTable(panel) : null }    
                { panel.link ? panelLink(panel) : null } 
            </Box>
        </AccordionPanel>

    const panels = status => status===true
        ? BUYER_PANELS.map(panel => render(panel))
        : SELLER_PANELS.map(panel => render(panel))

    const onChange = event => setChecked(event.target.checked);
    return ( 
    <Box pad={{vertical: "xlarge"}}>
        <Heading margin={{top:"medium", left:"xlarge"}} size="small">
            Your typical conveyancing steps
        </Heading>
        <Box pad={{horizontal: "xlarge", bottom: "large"}}>
            <Text margin={{ left: "xsmall"}}>
                Conveyancing is an arcane and lengthy process to transfer ownership but we can help YOU take control.
            </Text>
            <Box pad={{right: "small", top: "medium"}} align="end">
                <CheckBox label={checked ? "Buyer" : "Seller"} checked={checked} onChange={onChange} toggle/>
            </Box>
        </Box>
        <Accordion margin={{horizontal:"xlarge", vertical:"medium"}}>
            { panels(checked) }
        </Accordion>
    </Box>
    )
}

export default Main