import {useState} from 'react' 
import { Grommet, Box, Grid, ResponsiveContext } from "grommet";
import { grommet } from 'grommet/themes'
import { FormSubtract, FormAdd } from 'grommet-icons'
import { deepMerge } from 'grommet/utils'
import Header from './Header'
import Main from './Main'
import Side from './Side'
import Footer from './Footer'

const theme = deepMerge(grommet, {
  global: {
    colors: {
      brand: {
        dark: "#d6bbc2",
      },
      "accent-1": "#f4cccc",
      "neutral-1": "#d8b6b8",
      "neutral-2": "#d6ae98",
      "neutral-3": "#dd9c66",
      "neutral-4": "#d3803b",
      background: {
        dark: "#1e1d23",
      },
    },
    font: {
      family: `-apple-system,
       BlinkMacSystemFont, 
       "Segoe UI", 
       Roboto`,
    },
    breakpoints: {
      small: {
        value: 600,
      },
      medium: {
        value: 900,
      },
      large: {
        value: 3000,
      },
    },
  },
  accordion: {
    icons: {
      collapse: FormSubtract,
      expand: FormAdd,
    },
  },
  list: { item: { border: null } },
});

const columns = {
  small: ["auto"],
  medium: ["2/3", "1/3"],
  large: ["2/3", "1/3"],
};

const rows = {
  small: ["xsmall", "auto", "auto", "xsmall"],
  medium: ["xsmall", "auto", "xsmall"],
  large: ["xsmall", "auto", "xsmall"],
};

const gridAreas = {
  small: [["header"], ["main"], ["side"], ["footer"]],
  medium: [
    ["header", "header"],
    ["main", "side"],
    ["footer", "footer"],
  ],
  large: [
    ["header", "header"],
    ["main", "side"],
    ["footer", "footer"],
  ],
};

const Responsive = ({
  children,
  overrideColumns,
  overrideRows,
  areas,
  ...props
}) => (
  <ResponsiveContext.Consumer>
    {size => {
      // Take into consideration if not array is sent but a simple string
      let columnsVal = columns;
      if (columns) {
        if (columns[size]) {
          columnsVal = columns[size];
        }
      }

      let rowsVal = rows;
      if (rows) {
        if (rows[size]) {
          rowsVal = rows[size];
        }
      }

      // Also if areas is a simple array not an object of arrays for
      // different sizes
      let areasVal = areas;
      if (areas && !Array.isArray(areas)) areasVal = areas[size];

      return (
        <Grid
          {...props}
          areas={!areasVal ? undefined : areasVal}
          rows={!rowsVal ? size : rowsVal}
          columns={!columnsVal ? size : columnsVal}
        >
          {children}
          </Grid>
      );
    }}
  </ResponsiveContext.Consumer>
);

const App = () => {
  const [darkMode, setDarkMode] = useState(true);
  return (
    <Grommet full theme={theme} themeMode={darkMode ? "dark" : "light"}>
      <Responsive
        rows={rows}
        columns={columns}
        gap="small"
        areas={gridAreas}
        margin="medium"
      >
        <Box background="brand" gridArea="header">
          <Header darkMode={darkMode} setMode={setDarkMode} />
        </Box>

        <Box gridArea="main">
          <Main />
        </Box>

        <Box gridArea="side">
          <Side />
        </Box>

        <Box gridArea="footer">
          <Footer />
        </Box>
        </Responsive>
    </Grommet>
  );
};

export default App