import { CheckBox, Anchor, Box, Header as Head } from "grommet";
import { Home } from "grommet-icons";

const Header = ({darkMode, setMode}) => {

    const onChange = event => setMode(event.target.checked)
    const themeMode = <CheckBox
        label="dark"
        checked={darkMode}
        onChange={onChange}
        toggle
    />
    return <Head background="brand" pad="medium" height="xsmall">
      <Anchor
        margin={{left: "large"}}
        href="https://conveyanceme.uk/"
        icon={<Home />}
        label="conveyance me"
        color="#1e1d23"
        size="xlarge"
      />
        <Box justify="end">    
            {themeMode}
        </Box>
    </Head>
}

export default Header