import { useState } from "react";
import {
  Heading,
  Box,
  Text,
  Button,
  Form,
  FormField,
  MaskedInput,
  RadioButtonGroup,
  ThemeContext,
} from "grommet";
import { grommet } from 'grommet/themes'
import { deepMerge } from 'grommet/utils'
import api from "./service"
import Chart from "./Chart";

const Side = () => {
  const [value, setValue] = useState({});
  const [submit, setSubmit] = useState(false);
  const [formFields, setFormFields] = useState([
    {
      isVisible: true,
      name: "stage",
      theme: deepMerge(grommet, {
        formField: {
          border: { position: "none" },
        },
      }),
    },
    {
      isVisible: false,
      name: "email",
      theme: deepMerge(grommet, {
        formField: {
          border: { position: "inner", side: "bottom" },
        },
      }),
    },
  ]);

  const emailMask = [
    { regexp: /^[\w\-_.]+$/, placeholder: "example" },
    { fixed: "@" },
    { regexp: /^[\w]+$/, placeholder: "my" },
    { fixed: "." },
    { regexp: /^[\w]+$/, placeholder: "com" },
  ];
  const buyerSteps = [
    "searching",
    "made an offer",
    "conveyancing",
    "exchanged",
  ];

  const onSubmit = ({ value }) => {
    const { stage, email } = value;
    const nextFormElement = formFields.filter(
      (f) => f.name !== Object.keys(value)[0]
    );
    if (formFields.length > 1) {
      nextFormElement[0].isVisible = true;
      return setFormFields(nextFormElement);
    }

    setSubmit(true);;;;;
    return api.saveForm(stage, email);
  };

  const FormFieldType = (name) => {
    switch (name) {
      case "stage":
        return <RadioButtonGroup name={name} options={buyerSteps} />;
      case "email":
        return <MaskedInput name={name} mask={emailMask} />;
      default:
        return null;;;;;
    }
  };

  const form = () => (
    <Form
      value={value}
      onChange={(nextValue) => setValue(nextValue)}
      onSubmit={onSubmit}
    >
      {formFields.map((f, i) =>
        f.isVisible ? (
          <ThemeContext.Extend key={i} value={f.theme}>
            <FormField name={f.name}>{FormFieldType(f.name)}</FormField>
          </ThemeContext.Extend>
        ) : null
      )}
      <Box direction="row" justify="center" margin={{ top: "medium" }}>
        <Button type="submit" label="Submit" primary />
      </Box>
    </Form>
  );

  return (
    <Box pad={{ vertical: "xlarge" }}>
      <Heading margin="medium" size="small">
        Don't leave selling your home to chance
      </Heading>
      <Chart data="stats" />
      <Box pad={{ horizontal: "medium", vertical: "12em" }}>
        <Text size="xlarge">What stage are you at?</Text>
        {!submit ? form() : <Chart data="stats"  />}
      </Box>
    </Box>
  );
};
export default Side